body {
  background-color: #002525;
  scroll-behavior: smooth;
}

#nav {
  height: 80px;
  padding: 0, 30px;
  position: sticky;
  top: 0;
  z-index: 120;
}

#hero {
  width: 100%;
  height: 100vh;

  /* background-repeat: no-repeat;
  background-position: center;
  background-size: cover; */
}

#hero-contant {
  display: flex;
  justify-content: space-around;
  height: 100vh;
  align-items: center;
}
#hero-contant > div {
  width: 45%;
}

.hero-left {
  color: white;
  padding-left: 10%;
}
.left-text {
  font-size: 4vw;
}
.teal {
  color: #1dcfcf;
}

.hero-right > img {
  height: 80%;
  width: 80%;
  border-bottom: #1dcfcf 5px solid;
  border-right: #1dcfcf 3px solid;
  border-radius: 50%;
}
.br-section {
  border-bottom: #1dcfcf 3px solid;
  width: 85%;
  margin: 0 auto;
}

/* ========Hero ends =============== */

/* ====== Projects starts ============ */

.hr-component {
  width: 120px;
  margin: 0 auto;
  border: #7cf8f8 1px solid;
}

.cardBg {
  background-color: #215555;
}
.cardImage {
  height: 150px;
  padding: 5px;
}

/* ===== Skills start ================ */
.skillsImg {
  height: 100px;
  width: 100px;
  border: #1dcfcf solid 2px;
  border-radius: 50%;
  margin: 5px 10px;
  padding: 5px;
  color: #288383;
  fill: currentColor;
  display: flex;
  justify-self: center;
}

.skillsTitle {
  display: flex;
  justify-self: center;
  align-self: center;
  color: teal;
  font-weight: 600;
  text-shadow: #23a7a7 -2px -3px 32px;
}

/* ==== conatact start ================== */
.socialImg {
  height: 50px;
  width: 50px;
  border: #055555 solid 2px;
  border-radius: 50%;
  margin: 1px 15px 1px 0;
  padding: 5px;
  color: #289696;
  fill: currentColor;
}
.socialTitle > a {
  width: max-content;
  font-size: 20px;
  text-decoration: none;
  color: #1dcfcf;
  display: flex;
  align-items: center;
}
